@import url(https://fonts.googleapis.com/css?family=Poppins:800&family=Ubuntu:wght@400,700&display=swap);
html {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mobileContainer {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: none;
    z-index: 999;
}
.mobileContainer  >img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (max-width: 640px) {
    .mobileContainer {
        display: block;
    }
}
.App {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: 'Source Code Pro';
  font-weight: 300;
  color: #FFB4E8;
  background: linear-gradient(110deg, #FF5DC3, #FF7AF2 70%, #9039FF 100%); }
  .App .header {
    position: fixed;
    top: 20px;
    left: 25px;
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    font-weight: 800;
    letter-spacing: 3.82px;
    color: #DF3A93; }
  .App .mode {
    display: flex;
    justify-content: space-between;
    margin: 0 20px;
    margin-top: 75px; }
    .App .mode .item {
      cursor: not-allowed;
      font: 700 50px 'Poppins', sans-serif;
      text-transform: uppercase;
      color: #FF5999; }
      .App .mode .item.active {
        cursor: pointer;
        color: white; }
      .App .mode .item.multi {
        color: #F259FF; }
  .App .subtitle {
    font: 400 18px 'Ubuntu', sans-serif;
    position: fixed;
    top: 68%;
    left: 20px;
    width: 100%; }
    .App .subtitle .suffix span {
      margin-right: 20px; }
    .App .subtitle .suffix .activeSuffix {
      color: white;
      font-weight: 700; }
    .App .subtitle .suffix input[type="radio"] {
      border: solid 1px #FF43B4;
      background: none; }
  .App form {
    position: fixed;
    left: 0;
    top: calc(50% - 50px);
    width: 100%;
    height: 100px; }
    .App form input {
      width: 100%;
      height: 100px;
      background: linear-gradient(110deg, #D74F9C, #F46AA0 50%, #DC52FF 100%);
      color: white;
      outline: none;
      padding: 15px 25px;
      font-family: 'Ubuntu';
      font-weight: 700;
      font-size: 30px;
      border: none; }
      .App form input::-webkit-input-placeholder {
        color: #9a376f; }
      .App form input::-moz-placeholder {
        color: #9a376f; }
      .App form input:-ms-input-placeholder {
        color: #9a376f; }
      .App form input::-ms-input-placeholder {
        color: #9a376f; }
      .App form input::placeholder {
        color: #9a376f; }
    .App form button {
      margin-left: -60px;
      background-color: inherit;
      border: none;
      outline: none;
      color: white;
      font-size: 3rem;
      cursor: pointer; }
  .App .reset {
    position: fixed;
    left: 20px;
    top: 35%;
    background-color: #ff7600;
    border: none;
    outline: none;
    color: white;
    cursor: pointer;
    padding: 10px 20px;
    font: 400 16px 'Ubuntu'; }
    .App .reset:hover {
      background-color: #FF5EC4; }
  .App .errorMessage {
    margin: 20px auto;
    height: 20px;
    font-size: 1.5rem;
    color: red; }
  .App .scoreContainer {
    position: fixed;
    left: 30px;
    bottom: 20px;
    font: bold 85px 'Ubuntu', sans-serif; }
    .App .scoreContainer .score {
      color: white; }
    .App .scoreContainer .speed {
      color: #FF5EC4; }

